.test_card {
  width: 100%;
  height: 70vh;
  display: flex;
  flex-direction: column;
  &__question {
    text-transform: uppercase;
    width: 100%;
    text-align: center;
    margin: 2em auto; }
  &__btn_container {
    width: 90%;
    margin: 0 auto 3em;
    flex-grow: 1;
    align-items: flex-end;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly; }
  &__answers {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
  &__wrapper {
    display: flex;
    min-width: 300px;
    width: 60%;
    margin: 2em auto; } }


